html {
  box-sizing: border-box;
}
body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen-Sans, Ubuntu, Cantarell,
    'Helvetica Neue', sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol';
  background-image: url('./images/travel-search-illustration.jpg');
  background-size: cover;
  background-repeat: no-repeat;
  text-align: center;
}
* *::before,
*::after {
  box-sizing: inherit;
}
ul {
  list-style: none;
  padding: 0;
  margin: 0;
}
.country-item {
  font-size: 28px;
}
.card-title {
  font-size: 32px;
  color: #093131;
}
.card-img {
  width: 350px;
  padding-top: 10px;
}
.input_name {
  margin-top: 80px;
  margin-bottom: 10px;
  font-size: 40px;
  color: #0b5353;
}
.input {
  width: 250px;
  height: 36px;
  border: 2px solid #4b8f8f;
  border-radius: 50px;
  background-color: transparent;
  padding-left: 18px;
  padding-top: 15px;
  padding-bottom: 15px;
}
.input::placeholder {
  font-weight: 600;
  font-size: 15px;
  color: #093131;
}
.card-wrapper {
  margin: 10px auto;
  padding: 15px;
  width: 400px;
  background-color: rgba(255, 255, 255, 0.397);
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
    0px 2px 1px rgba(0, 0, 0, 0.2);
  border-radius: 0px 0px 4px 4px;
}
.card-item {
  font-weight: 600;
  color: #093131;
}
